<template>
  <body>
    <div id="backgroundTriangle"></div>
    <HeaderLogo />
    <Notification />
    <header id="headerLeft">
      <Navigation />
      <UserCard />
    </header>
    <main id="mainVideoSection">
      <header id="videoSectionHeader">
        <h1 class="titles">Linki</h1>
        <div id="videoSectionHeaderContainer">
          <StatisticBlock :type="'videos'" :hasLink="false" />
          <div id="videoSearchArea">
            <div id="videoSearchBorder">
              <input
                id="videoSearchInput"
                type="text"
                v-model="videoSearch"
                placeholder="Wyszukaj film"
              />
              <svg-icon
                @click="videoSearch = ''"
                class="pointer"
                type="mdi"
                :path="searchIcon"
              ></svg-icon>
            </div>
          </div>
        </div>
      </header>
      <section id="videoSection">
        <div id="videoSectionScroll">
          <div v-if="videos.length === 0" style="transform: translateY(0);" class="emptyValuesMessage">
            <h1>Brak linkÃ³w do wyÅwietlenia</h1>
          </div>
          <div
            class="videoSectionElement"
            v-for="video in filteredVideos"
            :key="video._id"
            :class="{ activeVideo: activeVideo === video._id }"
            @click="activeVideo = video._id"
          >
            <svg-icon
              class="videoSectionElementIcon"
              type="mdi"
              :path="iconsStore.mdiMovieOutline"
            ></svg-icon>
            <div class="videoSectionElementText">
              <span>{{ video.nick }}</span>
              <h1>{{ video.game }}</h1>
            </div>
            <div class="videoSectionElementButtons">
              <router-link :to="video.link">
                <svg-icon type="mdi" :path="iconsStore.mdiOpenInNew"></svg-icon>
              </router-link>
              <svg-icon
                @click="deleteVideo(video)"
                :class="{ visible: activeVideo === video._id }"
                class="videoSectionElementDelete pointer"
                type="mdi"
                :path="iconsStore.mdiTrashCanOutline"
              ></svg-icon>
            </div>
            <div class="videoSectionElementDesc">
              <span>
                {{ video.desc }}
              </span>
            </div>
            <svg-icon
              style="--sx: 1.5; --sy: 1.5"
              class="videoSectionElementResizeIcon"
              type="mdi"
              :path="iconsStore.mdiResizeBottomRight"
            ></svg-icon>
          </div>
        </div>
      </section>
    </main>
  </body>
</template>

<script>
import API from "../api";
import svgIcon from "@jamescoyle/vue-icon";
import popupSound from "../assets/audio/popup.wav";

import { useAlertsStore } from "../stores/alertsStore";
import { usePreferenceStore } from "../stores/preferenceStore";
import { useIconsStore } from "../stores/iconsStore";

import HeaderLogo from "../components/HeaderLogo.vue";
import Notification from "../components/Assets/Notification.vue";
import UserCard from "../components/Assets/UserCard.vue";
import Navigation from "../components/Dashboard/MainNavigation.vue";
import StatisticBlock from "../components/Dashboard/StatisticBlock.vue";

export default {
  name: "LinksView",
  components: {
    svgIcon,
    HeaderLogo,
    Notification,
    UserCard,
    Navigation,
    StatisticBlock,
  },
  setup() {
    const preferenceStore = usePreferenceStore();
    const alertsStore = useAlertsStore();
    const iconsStore = useIconsStore();

    return { preferenceStore, alertsStore, iconsStore };
  },
  data() {
    return {
      activeVideo: 0,
      searchIcon: this.iconsStore.mdiMagnify,
      videoSearch: "",
      videos: [],
    };
  },
  methods: {
    async deleteVideo(video) {
      const response = await API.deleteLink(video);

      await this.alertsStore.showNotification(
        response.duration,
        response.status,
        response.message
      );

      if (this.preferenceStore.preferences[0].state) {
        const notificationSound = new Audio(popupSound);
        notificationSound.volume = 0.5;
        notificationSound.play();
      }

      if (response.status === "success") {
        this.activeVideo = 0;
        this.videos = await API.getAllLinks();
      }
    },
  },
  watch: {
    videoSearch() {
      if (this.videoSearch.length === 0)
        this.searchIcon = this.iconsStore.mdiMagnify;
      else this.searchIcon = this.iconsStore.mdiTrashCanOutline;
    },
  },
  computed: {
    filteredVideos() {
      return this.videos.filter((video) => {
        return video.game
          .toLowerCase()
          .includes(this.videoSearch.toLowerCase());
      });
    },
  },
  async beforeMount() {
    this.videos = await API.getAllLinks();
  },
};
</script>

<style lang="scss" scoped>
body {
  background-color: #20222a;

  #mainVideoSection {
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 75%;

    #videoSectionHeader {
      width: 60%;
      margin: auto;

      #videoSectionHeaderContainer {
        display: flex;
        flex-flow: row nowrap;
        justify-content: space-between;
        align-items: center;
        width: 100%;
      }
    }

    #videoSearchArea {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 50%;
      height: 100px;
      border-radius: 5px;
      padding: 20px;
      background: linear-gradient(to bottom right, #20232a, #1a1c23);
      box-shadow: 0 4px 4px #181920;

      #videoSearchBorder {
        display: flex;
        flex-flow: row nowrap;
        justify-content: center;
        align-items: center;
        width: 100%;
        border: 1px solid #0c447b;
        border-radius: 5px;

        #videoSearchInput {
          width: 100%;
          padding: 5px;
          background: none;
          outline: none;
          border: none;
          color: #fff;
          font-size: 20px;
          font-family: "Lato", sans-serif;

          &::placeholder {
            text-align: center;
          }
        }

        svg {
          width: 10%;

          &:hover {
            color: #999;
          }
        }
      }
    }

    #videoSection {
      width: 60%;
      height: 450px;
      border-radius: 5px;
      padding: 20px;
      margin: 3em auto;
      background: linear-gradient(to bottom right, #20232a, #1a1c23);
      box-shadow: 0 4px 4px #181920;
      transition: 0.2s ease-in-out;

      #videoSectionScroll {
        display: flex;
        flex-flow: row wrap;
        height: 100%;
        width: 100%;
        margin: 0;
        padding: 20px;
        overflow-y: scroll;

        &::-webkit-scrollbar {
          width: 10px;
        }

        &::-webkit-scrollbar-track {
          background: #282c35;
          border-radius: 5px;
        }

        &::-webkit-scrollbar-thumb {
          background: #105192;
          border-radius: 5px;
        }

        &::-webkit-scrollbar-thumb:hover {
          background: #0e4275;
        }

        .videoSectionElement {
          position: relative;
          display: flex;
          flex-flow: row wrap;
          justify-content: center;
          align-items: center;
          width: 300px;
          height: 100px;
          margin: 20px;
          border-radius: 15px;
          background: linear-gradient(to bottom right, #20232a, #1a1c23);
          box-shadow: 0 4px 4px #181920;
          transition: 0.2s ease-in-out;
          overflow: hidden;
          cursor: pointer;

          &.activeVideo {
            width: 500px;
            height: 200px;
            cursor: default;
          }

          .videoSectionElementIcon {
            width: 30%;
            height: 40%;
          }

          .videoSectionElementText {
            width: 70%;
            height: 100px;
            display: flex;
            flex-flow: column nowrap;
            justify-content: center;
            text-align: start;

            span {
              margin: 5px 0;
              font-size: 20px;
              background: linear-gradient(
                to bottom right,
                rgb(102, 102, 102),
                rgba(102, 102, 102, 0)
              );
              background-clip: text;
              -webkit-background-clip: text;
              -webkit-text-fill-color: transparent;
            }

            h1 {
              font-size: 24px;
              margin: 0;
            }
          }

          .videoSectionElementDesc {
            display: flex;
            width: 90%;
            height: 50%;
          }

          .videoSectionElementResizeIcon {
            position: absolute;
            bottom: 10px;
            right: 10px;
            color: #d0d0d040;
          }

          .videoSectionElementButtons {
            position: absolute;
            top: 15px;
            right: 15px;
            height: 75px;
            display: flex;
            flex-flow: column nowrap;
            align-items: center;
            justify-content: space-evenly;

            .videoSectionElementDelete {
              visibility: hidden;
              transition: 0.2s ease-in-out;

              &.visible {
                visibility: visible;
              }
            }

            svg {
              color: #666;

              &:hover {
                color: #999;
              }
            }
          }
        }
      }
    }
  }
}
</style>