<template>
  <div class="block">
    <svg-icon class="blockIcon" type="mdi" :path="blockIcon"></svg-icon>
    <div class="blockText">
      <h1>{{ blockTitle }}</h1>
      <span>{{ blockDesc }}</span>
    </div>
    <router-link :to="`/dashboard/${blockLink}`" v-if="hasLink">
      <svg-icon type="mdi" :path="iconsStore.mdiOpenInNew"></svg-icon>
    </router-link>
  </div>
</template>

<script>
import svgIcon from "@jamescoyle/vue-icon";

import { useIconsStore } from "../../stores/iconsStore";
import { useGeneralStore } from "../../stores/generalStore";

export default {
  name: "StatisticsBlock",
  props: ["type", "hasLink"],
  components: {
    svgIcon,
  },
  setup() {
    const iconsStore = useIconsStore();
    const generalStore = useGeneralStore();

    return { generalStore, iconsStore };
  },
  data() {
    return {
      blockIcon: "",
      blockTitle: "",
      blockDesc: "",
      blockLink: "",
    };
  },
  async beforeMount() {
    await this.generalStore.getStatistics();

    if (this.type === "users") {
      this.blockIcon = this.iconsStore.mdiAccountMultipleOutline;
      this.blockTitle = "UÅ¼ytkownicy";
      this.blockDesc = this.generalStore.statistics.users;
      this.blockLink = "uzytkownicy";
    } else if (this.type === "videos") {
      this.blockIcon = this.iconsStore.mdiMovieOutline;
      this.blockTitle = "Odebrane filmy";
      this.blockDesc = this.generalStore.statistics.links;
      this.blockLink = "linki";
    } else if (this.type === "tasks") {
      this.blockIcon = this.iconsStore.mdiCalendarMonthOutline;
      this.blockTitle = "Do zrobienia";
      this.blockDesc = this.generalStore.statistics.tasks;
      this.blockLink = "zadania";
    } else {
      this.blockIcon = this.iconsStore.alertOctagonIcon;
      this.blockTitle = "Nieznany typ";
      this.blockDesc = NaN;
      this.blockLink = "NotFound";
    }
  },
};
</script>

<style lang="scss" scoped>
.block {
  position: relative;
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  align-items: center;
  width: 300px;
  height: 100px;
  padding: 10px 0;
  border-radius: 15px;
  background: linear-gradient(to bottom right, #20232a, #1a1c23);
  box-shadow: 0 4px 4px #181920;

  .blockIcon {
    width: 30%;
    height: 40px;
  }

  .blockText {
    width: 70%;
    height: 80%;
    margin: auto;
    display: flex;
    flex-flow: column nowrap;
    text-align: start;

    h1 {
      margin: 5px 0;
      font-size: 20px;
      background: linear-gradient(
        to bottom right,
        rgb(102, 102, 102),
        rgba(102, 102, 102, 0)
      );
      background-clip: text;
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
    }

    span {
      font-size: 24px;
    }
  }

  a {
    position: absolute;
    top: 20px;
    right: 20px;
    color: #666;

    &:hover {
      color: #999;
    }
  }
}
</style>